import { _decorator, Component, EventTouch, instantiate, Node } from "cc";
import { basePageView } from "../common/basePageView";
const { ccclass, property } = _decorator;

@ccclass("discoverView")
export class discoverView extends basePageView {
  @property(Node)
  btn_shift_game: Node = null;

  @property(Node)
  btn_shift_ugc: Node = null;

  @property(Node)
  game_part: Node = null;

  @property(Node)
  ugc_part: Node = null;

  @property(Node)
  instance: Node = null;

  @property(Node)
  father_node: Node = null;

  shift_btns: Node[] = [];

  start() {
    this, (this.shift_btns = [this.btn_shift_game, this.btn_shift_ugc]);
  }

  update(deltaTime: number) {}

  shiftCurrentPlayType(btn: EventTouch, data: string) {
    this.shift_btns.forEach((node) => {
      node.getChildByName("selected").active = false;
      node.getChildByName("selected_lable").active = false;
      node.getChildByName("unselected_lable").active = true;
    });
    btn.currentTarget.getChildByName("selectBg").active = true;
    btn.currentTarget.getChildByName("selected_lable").active = true;
    if (data === "game") {
      // this.game_part.active = true
      // this.ugc_part.active = false
    } else {
      // this.game_part.active = false
      // this.ugc_part.active = true
    }
  }

  initGameViews(
    gameList: {
      desc: string;
      hot: number;
      id: number;
      name: string;
      position: number;
      sort: number;
      tags: string[];
      type: number;
      url: string;
    }[]
  ) {
    gameList.forEach((game) => {
        const game_point = instantiate(this.instance)
        game_point.getChildByName()
    });
  }

  initUgcViews(
    ugcList: {
      desc: string;
      hot: number;
      id: number;
      name: string;
      position: number;
      sort: number;
      tags: string[];
      type: number;
      url: string;
    }[]
  ) {
    ugcList.forEach((ugc) => {});
  }

  currentPageClose() {}
}
